<template>
  <section>
<!--    <div class="hypContainer">-->
<!--      <ul class="innerHypContainer">-->
<!--        <li class="hypItem" v-for="item in $store.state[$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists">-->
<!--          <SumMain :list="item.hypTradeList" :key="item.id" short="true" :title="item.title"/>-->
<!--        </li>-->
<!--      </ul>-->
<!--    </div>-->
    <SubTitle title="Hypothetical portfolios"/>
    <div class="buttonsContainer">
      <ButtonList :current-tab.sync="currentTab" :tabs-list="cTabs"/>
      <button class="tabButton" @click="addTab">+</button>
      <button class="delBox hover" @click="deleteTab">
        x
      </button>
    </div>
    <div class="fc"><div class="card p5"><span class="grey">Date of creation: </span> {{ cDate}}</div></div>
    <div class="fc"><input placeholder="change title" class="card p5 white" v-model="name" v-on:keyup.enter="(ev) => {ev.target.blur();changeTitle()}"></div>
    <Main :list="cList" style="padding-top:50px"/>
  </section>
</template>

<script>
  import Main from './Main'
  import generateRandomId from '../../helpers/generateRandomId';
  import SubTitle from '../../components/SubTitle';
  import ButtonList from '../../components/ButtonList';
  import TradeForm from '../../components/TradeForm';
  import SumMain from '../../components/sum/SumMain';
  export default {
    components: {
      SumMain,
      TradeForm,
      ButtonList,
      SubTitle,
      Main,
    },
    props:['list'],
    data() {
      return {
        // currentTab: '',
        currentTab: this.list?this.list[0].title:this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists[0]?
          this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists[0].title
          :'',
        name:''
      };
    },
    methods:{
      changeTitle(){
        this.$store.commit('changeTitleHyp',{newName:this.name, oldName:this.currentTab,info:this.$store.state.currentInfo});
        this.currentTab = this.name
        this.name = ''
      },
      addTab() {
        const newTabName = prompt('Название гипотетической сборки')
        if (newTabName) {
          const item = {
            title:newTabName,
            id:generateRandomId(),
            hypTradeList:[]
          }
          this.currentTab = newTabName;
          this.$store.commit('addItemToList', { item,listName:'hypotheticalLists'});
        }
      },
      deleteTab(){
        const ok = confirm('delete tab?')
        if(ok){
          const id = this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists.find(item=>item.title===this.currentTab).id
          this.$store.commit('deleteItem', { id, listName:'hypotheticalLists'})
          this.currentTab = this.cTabs[this.cTabs.length-1];//сдвинуть таб посл стр
        }
      },
    },
    computed: {
      cTabs() {
        if(this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists.length){
          return this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists.map(item=>item.title)
        }else return [];
      },
      cList(){
        if(this.list&&this.list.length){
          const listObj = this.list.find(item=>item.title===this.currentTab)
          return listObj.hypTradeList || listObj.items
        }else if(this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists.length){
          const listObj = this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists.find(item=>item.title===this.currentTab)
          return listObj.hypTradeList || listObj.items
        }else return []
      },
      cDate(){
        if(this.cList.length && this.cList[0].trades){
          return this.cList[0].trades[0].date
        }else return 'no Date'
      }
    },
    watch: {
      '$store.state.currentInfo': {
        handler(){
          this.currentTab = this.list?this.list[0].title:this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists.length?
            this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists[0].title
            :''
        },
        immediate:false,
      },
    },
  };
</script>

